@import url(https://fonts.googleapis.com/css?family=Righteous);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
#content {
  display: flex;
  flex-flow: row nowrap;
  /*
    Content takes the viewport height
    minus the header height.
  */
  height: 100vh;
  box-sizing: border-box;
  margin-top: -100px;
  padding-top: 100px;
}

@media screen and (max-device-width: 500px) {
  #content {
    flex-flow: column nowrap;
  }
}
.header {
  height: 100px;
  background-color: #0B0C10;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .title {
  font-family: "Righteous", sans-serif;
  font-size: 35px;
  font-weight: 400;
  color: #66FCF1;
}
.header small {
  font-size: 12pt;
  vertical-align: middle;
}
.header hr {
  height: 1px;
  border: none;
  background-color: #45A29E;
  margin: 3px 0px -10px 0px;
}
.header .subtitle {
  font-size: 14pt;
  font-weight: 200;
  color: #45A29E;
}

@media screen and (max-device-width: 500px) {
  .header .title {
    font-size: 25px;
  }
  .header small {
    font-size: 8pt;
  }
  .header .subtitle {
    font-size: 10pt;
  }
}
.search {
  display: flex;
  flex-flow: column nowrap;
  width: 300px;
  background-color: #1F2833;
}
.search h2 {
  font-family: Arial, Helvetica, sans-serif;
  color: #45A29E;
}
.search input {
  padding: 10px 15px;
  margin: 10px;
}
.search-results {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.search-results li {
  padding: 10px 15px;
  text-align: left;
  width: 100%;
  color: #929496;
  -webkit-transition: color 0.3s, background-color 0.3s;
  transition: color 0.3s, background-color 0.3s;
}
.search-results li:hover {
  color: #C5C6C7;
  background-color: #465a72;
  cursor: pointer;
}

@media screen and (max-device-width: 500px) {
  .search {
    order: 2;
    width: 100%;
    flex-grow: 1;
  }
}
.map-container {
  width: 100%;
  flex-grow: 1;
}
.map {
  height: 100%;
}

@media screen and (max-device-width: 500px) {
  .map-container {
    order: 1;
    flex-grow: 0;
  }
  .map {
    padding-top: 100%; /* 1:1 Aspect Ratio */
    height: auto;
  }
}
.info-header {
  display: flex;
  flex: row 1 nowrap;
  padding-top: 10px;
}
.info-header img {
  flex: 0 1 auto;
}
.info-header h2 {
  width: 150px;
  margin: auto;
  padding-left: 10px;
  padding-right: 10px;
}
.info-header a {
  color: #45A29E;
  text-decoration: none;
}
